body, html {
  font-family: Raleway;
  -webkit-font-smoothing: antialiased;
}

.homepageTextContainer {
  position: absolute;
  text-transform: uppercase;
  text-decoration: none;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 100%;
  height: 22%;
}

.header {
  letter-spacing: 2px;
  font-size: 2.0em;
  font-weight: 700;
  margin-bottom: 5px;
  cursor: default;
}

.subHeader {
  letter-spacing: 1px;
  font-size: 1.5em;
  font-weight: 500;
  margin-bottom: 0;
  cursor: default;
}



